import { useState } from "react";
import { TextField, Slider, MenuItem, Select,
FormControl, InputLabel, Autocomplete } from "@mui/material";


const CustomInput = ({ type, range, options }) => {
  const [value, setValue] = useState("");

  const handleChange = (event) => {
    setValue(event.target.value);
  };

  const renderInput = () => {
    switch (type) {
      case "text":
        return <TextField value={value} onChange={handleChange} />;
      case "range":
        return (
          <Slider
            value={value}
            min={range.start}
            max={range.end}
            onChange={(event, newValue) => setValue(newValue)}
          />
        );
      case "options":
          if(options?.multiSelect) {
            return (
                <FormControl sx={{ m: 1, minWidth: 120 }}>
                    <Autocomplete
                        multiple
                        id="tags-outlined"
                        options={options?.values}
                        getOptionLabel={(option) => option}
                        defaultValue={[options?.values[0]]}
                        filterSelectedOptions
                        renderInput={(params) => (
                        <TextField
                            {...params}
                            label={options.label}
                            placeholder="Select"
                        />
                        )}
                    />                
                </FormControl>
            );
          } else {
            return (
                <FormControl sx={{ m: 1, minWidth: 120 }}>
                <InputLabel id="demo-simple-select-helper-label">{options.label}</InputLabel>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  value={value}
                  label={options.label}
                  onChange={handleChange}
                >
                  <MenuItem value="">
                    <em>None</em>
                  </MenuItem>
                  {options.values.map((option, index) => (
                  <MenuItem key={index} value={option}>
                    {option}
                  </MenuItem>
                ))}
                </Select>
              </FormControl>
            );
          }
      default:
        return <TextField value={value} onChange={handleChange} />;
    }
  };

  return renderInput();
};

export default CustomInput;
