import "./App.css";
import { Route, Routes, NavLink } from "react-router-dom";
import LocalCafeIcon from "@mui/icons-material/LocalCafe";
import ReceiptIcon from "@mui/icons-material/Receipt";
import AccountBoxIcon from "@mui/icons-material/AccountBox";
import HomePage from "./components/HomePage.js";
import SurveyPage from "./components/SurveyPage";
import {Login} from "./components/Login";
import useAuth from "./components/useAuth";
import { useNavigate } from 'react-router-dom'
import { useEffect, useState } from "react";
import {FormControl, Button, Typography} from '@mui/material';

const Account = (props) => {
  const { isLoggedIn, setLoggedIn } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    if (!isLoggedIn) {
      // console.log("After reloading II, ", props.isLoggedIn);
      // props.history.push("/login");
      navigate('/login');
      // return <Login {...props} setLoggedIn={props.setLoggedIn} />;
    }  
  }, [navigate, isLoggedIn])

  const handleLogout = (e) => {
    e.preventDefault();
    // setUserLoggedIn(loginSuccess);
    setLoggedIn({ isLoggedIn: false });
  };

  return (
    <div>
      <h2> Hey User!</h2>
      <div>
      <Typography>You are logged in!</Typography>
      </div>
      <div>
        <form onSubmit={handleLogout}>
        <FormControl>
        <Button variant="contained" color="primary" type="submit">
        Logout
        </Button>
        </FormControl>
        </form>
      </div>
    </div>
  );
};

export default function App() {
  const [currentSurvey, setCurrentSurvey] = useState();
  // useEffect(() => {
  //   setLoggedIn(getLoggedInStatus());
  // }, []);
  return (
    <div className="App">
      <header>
      <h1>Welcome to your Research Contributions</h1>
      </header>
      {/* <Menu /> */}
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/home" element={<HomePage />} />
        <Route path="/survey" element={<SurveyPage />} />
        <Route path="/survey/:id" element={<SurveyPage rememberSurvey={setCurrentSurvey}/>} />
        <Route path="/account" element={<Account />} />
        <Route path="/login" element={<Login />} />
      </Routes>
      <div className="nav">
        <NavLink to="/home" className="nav__link">
          <LocalCafeIcon />
          <span className="nav__text"> Home </span>
        </NavLink>
        <NavLink to={`/survey${currentSurvey ? ('/' + currentSurvey) : ''}`} className="nav__link">
          <ReceiptIcon />
          <span className="nav__text"> Survey </span>
        </NavLink>
        <NavLink to="/account" className="nav__link">
          <AccountBoxIcon />
          <span className="nav__text"> Account </span>
        </NavLink>
      </div>
    </div>
  );
}
