import {
    List,
    ListItem,
    ListItemText,
    ListSubheader,
  } from "@mui/material";
import { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";

import { getSurveyNames } from "../controllers/SurveyController"

const Home = () => {
    const [surveyNames, setSurveyNames] = useState([]);

    useEffect(() => {
        const getData = async () => {
            setSurveyNames(await getSurveyNames())
        };
        getData();
    }, [])

    return (
      <List
        sx={{ width: '100%', bgcolor: 'background.paper' }}
        component="nav"
        subheader={
            <ListSubheader component="div" id="nested-list-subheader">
            Available Surveys
            </ListSubheader>
        }
      >
      {
                surveyNames.map(({id, name}, i) => (
                    <ListItem key={i} button component={NavLink} to={"/survey/"+id} >
                        <ListItemText primary={name} />
                    </ListItem>
                ))
      }
      </List>
    )
}

export default Home;