import loadMetaData from '../data/surveyData';

let metadata ;

export const fetchData = async () => {
    try {
        metadata = await loadMetaData();
    } catch (error) {
      console.error(error);
    }
};

fetchData();

export const getSurveyNames = async () => {
    if(!metadata){
        await fetchData();
    }
    const surveys = metadata["surveys"];
    return surveys.map(({id, name}) => ({id, name}))
}

export const getSurvey = async (id) => {
    if(!metadata) {
        console.log('Awaiting data')
        await fetchData();
    } 
    const surveys = metadata["surveys"];
    const survey = surveys.find((entry) => {
        return entry.id === id
    });
    return survey
}