import { useState } from "react";

export default function useAuth() {
  const getLoggedInStatus = () => {
    const loginStatusString = localStorage.getItem("loginStatus");
    const loginStatus = JSON.parse(loginStatusString);
    return loginStatus?.isLoggedIn;
  };

  const [isLoggedIn, setLoggedIn] = useState(getLoggedInStatus());

  const setLoggedInStatus = (loginStatus) => {
    localStorage.setItem("loginStatus", JSON.stringify(loginStatus));
    setLoggedIn(loginStatus.isLoggedIn);
  };

  return {
    isLoggedIn,
    setLoggedIn: setLoggedInStatus
  };
}
