import axios from 'axios';

const SERVER_URL = 'https://smart-mud-node.glitch.me/meta';
const secret = 'EZZ_LAB';
// Make a synchronous GET request to the API endpoint
async function fetchData() {
  try {
    const response = await axios.post(SERVER_URL, {secret});
    return response.data;
  } catch (error) {
    console.error(error);
    return null;
  }
}

// Call the fetchData function when the application loads
async function loadMetaData() {
  const data = await fetchData();
  return data;
}


export default loadMetaData;